@use 'variables' as *;

#menu {
    display: none;
}

aside {
    display: none
}

body {
    font-family: $ff;
    color: $cl-dark;
    font-size: $fs-body;
    font-weight: $fw-extralight;
    line-height: $lh-body;
    margin-top: 10px;
    background-color: $cl-light;
    margin: 0 auto;


}

.edp-fullwidth {
    width: 100%;
}

a:link,
a:visited,
a:hover,
a:active {
    color: $cl-link;
    text-decoration: none;
}


.grid12 {

    grid-template-columns: repeat(12, 1fr);
    display: grid;
    grid-column-gap: 24px;
    max-width: 1248px;
}

.span12 {
    grid-column: 1 / span 12;
    grid-template-columns: subgrid;
    display: grid;
}

.span10 {
    grid-column: 2 / span 10;
    grid-template-columns: subgrid;
}

.span8 {
    grid-column: 3 / span 8;
}