@use 'variables' as *;

.edp-fullwidth {
    width: 100%;
}

.edp-pagewidth {
    max-width: $mw-full;
    margin: 0 auto;
}

.grid12 {
    grid-template-columns: repeat(12, 1fr);
    display: grid;
    grid-column-gap: 24px;
    max-width: $mw-full;
}

.span12 {
    grid-column: 1 / span 12;
    grid-template-columns: subgrid;
    display: grid;
}

.span10 {
    grid-column: 2 / span 10;
    grid-template-columns: subgrid;
}

.span8 {
    grid-column: 3 / span 8;
}